<template>
  <div class="dashboard__cntr">
    <div class="dashboard__cntr__grid__cntr">
      <div class="grid__cntr--total__acounts"></div>
      <div class="grid__cntr--total__dcf"></div>
      <div class="grid__cntr--ongoing__dcf"></div>
      <div class="grid__cntr--completed__dcf"></div>
    </div>
    <div class="dashboard__cntr__graph__cntr">
      <div class="graph__cntr--first">
        <line-chart />
      </div>
      <div class="graph__cntr--second">
        <filled-line-chart />
      </div>
    </div>
    <div class="dashboard__cntr__table__cntr">
      <b-table
        :data="data"
        :columns="columns"
        :checked-rows.sync="checkedRows"
        checkable
        :checkbox-position="checkboxPosition"
      >
        <template slot="bottom-left">
          <b>Total checked</b>: {{ checkedRows.length }}
        </template>
      </b-table>
    </div>
  </div>
</template>

<script>
import lineChart from "./lineChart.vue";
import filledLineChart from "./filledLineChart.vue";

export default {
  name: "Dashboard",
  components: {
    lineChart,
    filledLineChart
  },
  data() {
    return {
      checkboxPosition: "left",
      checkedRows: [],
      data: [
        {
          id: 1,
          first_name: "Jesse",
          last_name: "Simmons",
          date: "2016-10-15 13:43:27",
          gender: "Male"
        },
        {
          id: 2,
          first_name: "John",
          last_name: "Jacobs",
          date: "2016-12-15 06:00:53",
          gender: "Male"
        },
        {
          id: 3,
          first_name: "Tina",
          last_name: "Gilbert",
          date: "2016-04-26 06:26:28",
          gender: "Female"
        },
        {
          id: 4,
          first_name: "Clarence",
          last_name: "Flores",
          date: "2016-04-10 10:28:46",
          gender: "Male"
        },
        {
          id: 5,
          first_name: "Anne",
          last_name: "Lee",
          date: "2016-12-06 14:38:38",
          gender: "Female"
        }
      ],
      columns: [
        {
          field: "id",
          label: "ID",
          width: "40",
          numeric: true
        },
        {
          field: "first_name",
          label: "First Name"
        },
        {
          field: "last_name",
          label: "Last Name"
        },
        {
          field: "date",
          label: "Date",
          centered: true
        },
        {
          field: "gender",
          label: "Gender"
        }
      ]
    };
  }
};
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="scss" scoped>
.dashboard__cntr {
  min-width: 80%;
  max-width: 100%;
  display: inline-block;
  vertical-align: middle;
  text-align: center;
  margin-top: 120px;
  margin-left: 300px;
  &__grid__cntr {
    .grid__cntr--total__acounts,
    .grid__cntr--total__dcf,
    .grid__cntr--ongoing__dcf,
    .grid__cntr--completed__dcf {
      display: inline-block;
      vertical-align: middle;
      width: 20%;
      height: 100px;
      margin-right: 50px;
      border: 5px solid #e5e5e5;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }
  }
  &__graph__cntr {
    .graph__cntr--first,
    .graph__cntr--second {
      display: inline-block;
      vertical-align: middle;
      border: 5px solid #e5e5e5;
      box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
      margin-top: 30px;
      margin-right: 50px;
      width: 44.5%;
      padding: 20px;
    }
  }
  &__table__cntr {
    margin-top: 40px;
    width: 98%;
    .table thead {
  background-color: #E5E5E5 !important;
}
  }
}
</style>



